import { Button } from "@mui/material";
import React, { Component } from "react";
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";
import CreateTimer from "../CreateTimer/CreateTimer";
import Timer from "../Timer/Timer";
import "./index.css";

// Reorder the list items
const reorder = (list, startIndex, endIndex) => {
  const result = Array.from(list);
  const [removed] = result.splice(startIndex, 1);
  result.splice(endIndex, 0, removed);

  return result;
};

const getItemStyle = (isDragging, draggableStyle) => ({
  userSelect: "none",
  ...draggableStyle,
});

const getListStyle = (isDraggingOver) => ({
  display: "grid",
  gridTemplateColumns: "1fr",
  gap: "20px",
  margin: "auto",
  padding: "45px 0",
});

class VerticalDragList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      items: props.data,
      deleteTimer: this.props.deleteTimer,
      open: false,
    };
    this.onDragEnd = this.onDragEnd.bind(this);
  }

  onDragEnd(result) {
    // if item dropped outside the list
    if (!result.destination) {
      return;
    }

    const items = reorder(
      this.state.items,
      result.source.index,
      result.destination.index
    );

    this.setState({
      items,
    });

    sessionStorage.setItem("data", JSON.stringify(items));
  }

  render() {
    return (
      <>
        <DragDropContext onDragEnd={this.onDragEnd}>
          <Droppable droppableId="droppable">
            {(provided, snapshot) => (
              <div
                {...provided.droppableProps}
                ref={provided.innerRef}
                style={getListStyle(snapshot.isDraggingOver)}
              >
                {this.state.items && this.state.items.length ? (
                  this.state.items.map((item, index) => (
                    <Draggable
                      key={item.id}
                      draggableId={item.id}
                      index={index}
                    >
                      {(provided, snapshot) => (
                        <div
                          ref={provided.innerRef}
                          {...provided.draggableProps}
                          {...provided.dragHandleProps}
                          style={getItemStyle(
                            snapshot.isDragging,
                            provided.draggableProps.style
                          )}
                        >
                          <Timer
                            data={item}
                            index={index}
                            deleteTimer={() => this.state.deleteTimer(index)}
                            key={Math.random() * index * Math.random()}
                          />
                        </div>
                      )}
                    </Draggable>
                  ))
                ) : (
                  <div className="centerContent">
                    <Button
                      className="button"
                      onClick={() => {
                        this.setState({ open: true });
                      }}
                    >
                      Create timer
                    </Button>
                  </div>
                )}
                {provided.placeholder}
              </div>
            )}
          </Droppable>
        </DragDropContext>
        <CreateTimer open={this.state.open} />
      </>
    );
  }
}

export default VerticalDragList;
