import { createContext, useEffect, useState } from "react";

export const Context = createContext({});

export const TimerProvider = (props) => {
  const [timerState, setTimerState] = useState({
    index: 0,
    on: false,
    totalTime: "00:00:00",
  });

  const getTimeFromLocalStorage = () => {
    // get data from session storage
    console.log("he;ll ");
    var dataArray = sessionStorage.getItem("data");
    // condition to check if data exists in session storage or not
    if (dataArray !== null) {
      dataArray.replace(/'/g, '"');
      var result = JSON.parse(dataArray);
      // taking out time element from array of objs
      var time = result.map((item) => item.time);
      var secondsArray = []; // array initialization for future use
      // conversion of hh:mm:ss into seconds and pushing into seconds array

      time.slice(timerState.index).map((item) => {
        var newTime = item.split(":");
        newTime =
          parseInt(newTime[0] * 60 * 60) +
          parseInt(newTime[1] * 60) +
          parseInt(newTime[2]);
        secondsArray.push(newTime);
      });
      // adding all the elements in seconds array
      var sum = secondsArray.reduce((partialSum, a) => partialSum + a, 0);
      // converting seconds into hh:mm:ss format
      var timeFormat = new Date(0);
      timeFormat.setSeconds(sum);
      var timeString = timeFormat.toISOString().substr(11, 8);
      // setting the context
      setTimerState({
        ...timerState,
        totalTime: timeString,
      });
    }
  };

  useEffect(() => {
    getTimeFromLocalStorage();
    console.log(timerState.index);
    console.log(timerState.totalTime);
  }, [timerState.index, timerState.on]);

  return (
    <Context.Provider value={[timerState, setTimerState]}>
      {props.children}
    </Context.Provider>
  );
};
