import * as React from "react";
import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import "./index.css";
import { Grid } from "@mui/material";
import ImageModal from "../ImageModal/ImageModal";

export default function InitialModal(props) {
  const avatars = [
    "witch_1.webp",
    "witch_2.webp",
    "witch_3.webp",
    "wizard_1.webp",
    "wizard_2.webp",
    "wizard_3.webp",
  ];

  const [open, setOpen] = React.useState(false);
  const [imageSource, setImageSource] = React.useState("");
  const handleClose = () => setOpen(false);

  return (
    <>
      <Dialog
        open={props.open}
        onClose={props.onlyClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogContent>
          <DialogContentText textAlign="center" id="alert-dialog-description">
            You are a coach, trainer, or work in the agile environment (work
            with SCRUM or other agile frameworks) and are a fan of timeboxing?
            Are you looking for a tool that supports you in scheduling meetings
            and workshops?
            <br />
            <br />
            Then you've come to the right place. That's precisely where we
            (PURPOSE IN PROGRESS) were some time ago and set out to develop our
            own tool. For the moment everything is free of charge, and we try to
            collect as little data as possible, just the data that's necessary
            to keep the application running. Additionally, we'll make the code
            available as open-source. The tool will be further developed
            whenever we feel like it. If you have any ideas or suggestions,
            please feel free to write to:{" "}
            <a href="">thkot@purposeinprogress.de</a>.
          </DialogContentText>
          <Grid item xs={12} sm={12}>
            <div className="avatar__wrapper">
              {avatars.map((avatar, index) => (
                <picture className={`avatar`} key={index}>
                  <img
                    onClick={() => {
                      setImageSource(avatar);
                      setOpen(true);
                    }}
                    src={process.env.PUBLIC_URL + avatar}
                    alt={avatar}
                  />
                </picture>
              ))}
            </div>
          </Grid>
        </DialogContent>
        <DialogActions sx={{ display: "flex", justifyContent: "center" }}>
          <Button
            className="button"
            onClick={() => props.setItemInSessionStorage()}
          >
            Give it a try!
          </Button>
        </DialogActions>
        <ImageModal imageSource={imageSource} open={open} close={handleClose} />
      </Dialog>
    </>
  );
}
