import React, { useState, useEffect } from "react";
import "./App.css";
import Container from "@mui/material/Container";
import Navbar from "./Components/Navbar/Navbar";
import VerticalDragList from "./Components/VerticalDragList/VerticalDragList";
import { TimerProvider } from "./Context/Context";
import InitialModal from "./Components/InitialModal/initialModal";

function App() {
  const [data, setData] = useState(JSON.parse(sessionStorage.getItem("data")));
  const [openModal, setOpenModal] = useState(false);

  useEffect(() => {
    let sessionStorageData = sessionStorage.getItem("accepted");
    if (sessionStorageData === null) {
      setOpenModal(true);
    }
  }, []);

  const deleteTimer = (i) => {
    let array = [...data.slice(0, i), ...data.slice(i + 1)];
    setData(array);
    sessionStorage.setItem("data", JSON.stringify(array));
    window.location.reload();
  };

  const setItemInSessionStorage = () => {
    sessionStorage.setItem("accepted", true);
    onlyCloseModal();
  };

  const onlyCloseModal = () => setOpenModal(false);

  return (
    <div className="App">
      <TimerProvider>
        <Navbar />
        <div className="App__container">
          <div className="image__one__container">
            <img src="/logo-1.png" className="image__one" alt="Logo" />
          </div>

          <Container className="App__container__inner" maxWidth="xl">
            <VerticalDragList data={data} deleteTimer={deleteTimer} />
          </Container>
          <a
            href="https://purposeinprogress.de/"
            className="image__two"
            target="_blank"
          >
            <img
              src="/purpose-in-progress.png"
              width={150}
              alt="Purpose_In_Progress"
            />
          </a>
        </div>
      </TimerProvider>
      <InitialModal
        open={openModal}
        onlyClose={onlyCloseModal}
        setItemInSessionStorage={setItemInSessionStorage}
      />
    </div>
  );
}

export default App;
