import { useContext, useState } from "react";
import "./index.css";
import Typography from "@mui/material/Typography";
import { CountdownCircleTimer } from "react-countdown-circle-timer";
import { Context } from "../../Context/Context";

const Timer = ({ data, index, deleteTimer }) => {
  const [timerContext, setTimerContext] = useContext(Context);
  const [on, setOn] = useState(false);
  const [key, setKey] = useState(0);

  const children = ({ remainingTime }) => {
    let hours = Math.floor(remainingTime / 3600);
    let minutes = Math.floor((remainingTime % 3600) / 60);
    let seconds = remainingTime % 60;
    hours = hours < 10 ? "0" + hours : hours;
    minutes = minutes < 10 ? "0" + minutes : minutes;
    seconds = seconds < 10 ? "0" + seconds : seconds;
    return `${hours}:${minutes}:${seconds}`;
  };

  const convertRawToTime = () => {
    let time = data.time.split(":");
    time =
      parseInt(time[0] * 60 * 60) + parseInt(time[1] * 60) + parseInt(time[2]);
    return time;
  };

  const playSound = () => {
    const beepAudio = new Audio(`beep.mp3`);
    beepAudio.play();
    setTimeout(() => {
      beepAudio.pause();
    }, 1000);
    // setTimerContext({ index: index + 1, on: true });
    setTimerContext({ ...timerContext, index: index + 1, on: false });
  };

  return (
    <div className="timer">
      <div className="timer__top">
        <picture className="avatar">
          <img
            className={
              timerContext.on && index === timerContext.index && "avatar__img"
            }
            src={process.env.PUBLIC_URL + data.avatar}
            alt={data.avatar}
          />
        </picture>

        <div className="timer__clock">
          <CountdownCircleTimer
            key={key}
            isPlaying={timerContext.on && index === timerContext.index}
            duration={convertRawToTime()}
            colors={["#2DF8E8", "#2DF8E8", "#2DF8E8", "#2DF8E8"]}
            colorsTime={[7, 5, 2, 0]}
            children={children}
            size={80}
            strokeWidth={6}
            onComplete={playSound}
          >
            {/* {({ remainingTime }) => remainingTime} */}
          </CountdownCircleTimer>
        </div>
      </div>

      <Typography variant="h6" className="timer__title">
        {data.name}
      </Typography>
      <div className="timer__buttons">
        <button
          onClick={() =>
            setTimerContext({ ...timerContext, index, on: !timerContext.on })
          }
        >
          Start / Pause
        </button>
        <button onClick={() => deleteTimer(index)}>Delete</button>
        <button onClick={() => (key === 0 ? setKey(1) : setKey(0))}>
          Reset
        </button>
      </div>
    </div>
  );
};
export default Timer;
