import React, { useContext } from "react";
import "./index.css";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";
import Container from "@mui/material/Container";
import CreateTimer from "../CreateTimer/CreateTimer";
import { Button } from "@mui/material";
import { Context } from "../../Context/Context";
import { CountdownCircleTimer } from "react-countdown-circle-timer";

const Navbar = () => {
  const [open, setOpen] = React.useState(false);
  const [timerContext, setTimerContext] = useContext(Context);
  const convertRawToTime = () => {
    let time = timerContext.totalTime.split(":");
    time =
      parseInt(time[0] * 60 * 60) + parseInt(time[1] * 60) + parseInt(time[2]);
    return time;
  };
  const renderTime = ({ remainingTime }) => {
    var timeFormat = new Date(0);
    timeFormat.setSeconds(remainingTime);
    var timeString = timeFormat.toISOString().substr(11, 8);
    return timeString;
  };
  return (
    <>
      <AppBar className="navbar" position="static">
        <Container maxWidth="xl">
          <Toolbar
            sx={{ display: "flex", justifyContent: "space-between" }}
            disableGutters
          >
            <Typography
              variant="h6"
              noWrap
              component="div"
              //sx={{ flexGrow: 1, display: { xs: "flex" } }}
              sx={{ display: { xs: "flex" } }}
            >
              <img src="/logo.png" width={100} alt="Logo" />
            </Typography>
            <Box>
              <div className="navbar__container">
                <CountdownCircleTimer
                  isPlaying={timerContext.on}
                  strokeWidth={0}
                  size={50}
                  duration={convertRawToTime()}
                  colors={["#004777", "#F7B801", "#A30000", "#A30000"]}
                  colorsTime={[10, 6, 3, 0]}
                  onComplete={() => ({ shouldRepeat: true, delay: 1 })}
                >
                  {renderTime}
                </CountdownCircleTimer>
              </div>
              <div className="navbar__button__container">
                <Button
                  size="small"
                  onClick={() =>
                    setTimerContext({
                      ...timerContext,
                      on: !timerContext.on,
                    })
                  }
                  className="navbar__button"
                >
                  Start / Pause
                </Button>
                <Button
                  size="small"
                  className="navbar__button"
                  onClick={() => window.location.reload()}
                >
                  Reset
                </Button>
              </div>
            </Box>
            <Box sx={{ flexGrow: 0 }}>
              <Button className="navbar__button" onClick={() => setOpen(true)}>
                Create timer
              </Button>
            </Box>
          </Toolbar>
        </Container>
      </AppBar>
      <CreateTimer open={open} setOpen={setOpen} />
    </>
  );
};
export default Navbar;
