import * as React from "react";
import "./index.css";
import Avatar from "@mui/material/Avatar";
import Button from "@mui/material/Button";
import CssBaseline from "@mui/material/CssBaseline";
import TextField from "@mui/material/TextField";
import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import AccessTimeIcon from "@mui/icons-material/AccessTime";
import Typography from "@mui/material/Typography";
import Container from "@mui/material/Container";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import Dialog from "@mui/material/Dialog";
import Snackbar from "@mui/material/Snackbar";

const theme = createTheme({
  palette: {
    primary: {
      main: "#2DF8E8",
    },
    secondary: {
      main: "#54595F",
    },
  },
});

export default function CreateTimer(props) {
  const [open, setOpen] = React.useState(false);
  const [selectedAvatar, setSelectedAvatar] = React.useState("witch_1.webp");

  const avatars = [
    "witch_1.webp",
    "witch_2.webp",
    "witch_3.webp",
    "wizard_1.webp",
    "wizard_2.webp",
    "wizard_3.webp",
    "pause.png",
  ];

  const S4 = () => {
    return (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1);
  };
  const guid = () => {
    return (
      S4() +
      S4() +
      "-" +
      S4() +
      "-4" +
      S4().substr(0, 3) +
      "-" +
      S4() +
      "-" +
      S4() +
      S4() +
      S4()
    ).toLowerCase();
  };

  const handleClose = () => {
    props.setOpen(false);
  };

  const validateTime = (time) => {
    return String(time)
      .toLowerCase()
      .match(/^([0-1]?[0-9]|2[0-3]):[0-5][0-9]:[0-5][0-9]$/);
  };

  const selectAvatar = (avatar) => {
    setSelectedAvatar(avatar);
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    const data = new FormData(event.currentTarget);
    data.append("avatar", selectedAvatar);
    data.append("id", guid());
    // eslint-disable-next-line no-console
    console.log("Hell", validateTime(data.get("time")));
    if (
      validateTime(data.get("time")) &&
      data.get("name") &&
      data.get("avatar")
    ) {
      let sessionData = JSON.parse(sessionStorage.getItem("data"));
      if (sessionData) {
        sessionData.push({
          name: data.get("name"),
          time: data.get("time"),
          avatar: data.get("avatar"),
          id: data.get("id"),
        });
        sessionStorage.setItem("data", JSON.stringify(sessionData));
      } else {
        sessionStorage.setItem(
          "data",
          JSON.stringify([
            {
              name: data.get("name"),
              time: data.get("time"),
              avatar: data.get("avatar"),
              id: data.get("id"),
            },
          ])
        );
      }
      setSelectedAvatar("witch_1.webp");
      window.location.reload();
    } else {
      setOpen(true);
    }
  };

  return (
    <>
      <Dialog open={props.open} onClose={handleClose}>
        <ThemeProvider theme={theme}>
          <Container component="main" maxWidth="xs">
            <CssBaseline />
            <Box
              sx={{
                // marginTop: 8,
                display: "flex",
                flexDirection: "column",
                alignItems: "center",
                paddingTop: 3,
                paddingBottom: 3,
              }}
            >
              <Avatar sx={{ m: 1, bgcolor: "secondary.main" }}>
                <AccessTimeIcon />
              </Avatar>
              <Typography component="h1" variant="h5">
                Create Timer
              </Typography>
              <Box
                component="form"
                noValidate
                onSubmit={handleSubmit}
                sx={{ mt: 3 }}
              >
                <Grid container spacing={2}>
                  <Grid item xs={12} sm={12}>
                    <TextField
                      autoComplete="given-name"
                      name="name"
                      required
                      fullWidth
                      id="name"
                      label="Name"
                      autoFocus
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <TextField
                      required
                      fullWidth
                      id="time"
                      label="Enter Time"
                      name="time"
                      placeholder="hh:mm:ss"
                    />
                  </Grid>
                  {/* <Grid item xs={12}>
                    <FormControlLabel
                      control={
                        <Checkbox value="allowExtraEmails" color="primary" />
                      }
                      label="I want to receive inspiration, marketing promotions and updates via email."
                    />
                  </Grid> */}

                  <Grid item xs={12} sm={12}>
                    <div className="avatar__wrapper">
                      {avatars.map((avatar, index) => (
                        <picture
                          className={`avatar ${
                            avatar === selectedAvatar ? "avatar-active" : ""
                          }`}
                          key={index}
                          onClick={() => selectAvatar(avatar)}
                        >
                          <img
                            src={process.env.PUBLIC_URL + avatar}
                            alt={avatar}
                          />
                        </picture>
                      ))}
                    </div>
                  </Grid>
                </Grid>
                <Button
                  type="submit"
                  fullWidth
                  variant="contained"
                  sx={{ mt: 3, mb: 2, fontWeight: "bold" }}
                >
                  Create
                </Button>
              </Box>
            </Box>
          </Container>
        </ThemeProvider>
      </Dialog>
      <Snackbar
        open={open}
        onClose={() => {
          setOpen(false);
        }}
        autoHideDuration={3000}
        message="Please enter valid data"
      />
    </>
  );
}
